import './App.css';
import profilepic from './images/profilepic.jpg'
import Display from './componets/Display'
import { useState } from 'react';
import Count from './componets/Count'



function App() {

  const [Tiltle, setTiltle] = useState("HELLO!")

  const handleName = () => {
    setTiltle('My name is Henry')
  }

  const handleSkills = () => {
    setTiltle("I'm a Frontend Web Developer")
  }

  const [counter, setCounter] = useState(0)

  return (
    <>
      <div className="container">
        <div className="profile">
          <img src={profilepic} />
        </div>
        <div className="popup">
            <Display tech={Tiltle} />
        </div>
        <div className="btn">
        
        
          <button id="Name" onClick={handleName} >Name</button>
          <button id="Skills" onClick={handleSkills} >Skills</button>

        </div>
        <div className="likes">
        
        <i class="fas fa-thumbs-up" onClick= {() => setCounter(counter + 1)} ></i>
        <Count counts = {counter} />
        </div>
        <div className="giticon">
          <a href="https://github.com/hefamba">
          <i class="fab fa-github-square" ></i>
          </a>
        </div>
      </div>


    </>
      
  );
}

export default App;
